import { UPDATE_STATE } from "reduxStore/actionTypes";

export const initialState = { state: "intro" };

export default function(state = initialState, action) {
  switch (action.type) {
    case UPDATE_STATE: {
      const { newState } = action.payload;
      return {
        ...state,
        state: newState
      };
    }
    default:
      return state;
  }
}
